import Vue from 'vue'
import Router from 'vue-router'
// import layout from '@/components/layout'
// import HelloWorld from '@/components/HelloWorld'
// import login from '@/components/login'
// import register from '@/components/register'
// import echart from '@/components/echart'


const layout = r => require.ensure([], () => r(require('@/components/layout')), 'group-home');
const HelloWorld = r => require.ensure([], () => r(require('@/components/HelloWorld')), 'group-home');
const login = r => require.ensure([], () => r(require('@/components/login')), 'login');
const register = r => require.ensure([], () => r(require('@/components/register')), 'group-home');
const echart = r => require.ensure([], () => r(require('@/components/echart')), 'group-home');
Vue.use(Router);

const routers={
  mode:'history',
  routes: [
    {
      path:'/',
      name:"login",
      component:login
    },
    {
      path:'/register',
      component:register
    },
    {
      path: '/layout',
      name: 'layout',
      component: layout,
      meta:{ requiresAuth: true },
      children:[
        {
          path:'/',
          component:HelloWorld,
          meta:{ requiresAuth: true }
        },
        {
          path:'/echart',
          component:echart,
          meta:{ requiresAuth: true }
        }
      ]
    }
  ]
}

const  router=new Router(routers);

router.beforeEach((to,from,next)=>{
  let token=window.localStorage.getItem('token');
  if(to.matched.some(record => record.meta.requiresAuth) && (!token || token === null)){
    next({
      path:'/',
      query:{redirect:to.fullPath }
    })
  }else{
    next()
  }
})

export default router



